.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 0;
    z-index: 999;
    background-color: var(--gradient-background);
    color: rgba(var(--color-foreground));
    // .header_line

    &_line {
        display: flex;
        flex-direction: row;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    // .header_logo

    &_logo {
        position: relative;
        z-index: 2;
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    // .header_nav

    &_nav {
        flex: 1 1 auto;
        &:not(:last-child) {
            margin-right: 15px;
        }
        @media (max-width: $md3 + px) {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: black;
            z-index: 1;
            visibility: hidden;
            opacity: 0;
            transform: translateX(-100%);
            pointer-events: none;
        }
        &.menu--active {
            visibility: visible;
            opacity: 0.9;
            transform: translateX(0);
            pointer-events: all;
        }
        transition: transform .3s ease-in-out, opacity .3s, visibility .3s;
    }

    // .header_list

    &_list {
        display: flex;
        justify-content: center;
        @media (max-width: $md3 + px) {
           flex-direction: column;
           align-items: center;
           height: 100%;
        }
    }

    // .header_list-item

    &_list-item {
        &:not(:last-child) {
            margin-right: 15px;
        }
        @media (max-width: $md3 + px) {
            &:not(:last-child) {
                margin-bottom: 15px;
            }
         }
    }

    // .header_list-link

    &_list-link {
        @media (max-width: $md3 + px) {
            font-size: 25px;
         }
    }

    // .header_contacts

    &_contacts {
        position: relative;
        z-index: 2;
        margin-left: auto;
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    // .header_phone

    &_phone {

    }
}

.header-logo {
    display: block;
    width: 60px;
    img {
        display: block;
        max-width: 100%;
        height: auto;
        vertical-align: top;
    }
    @media (min-width: $md2 + px) {
        width: 100px;
    }

}

.header-phone {
    font-size: 16px;
    font-weight: 600;
    // .header-phone_icon
    &:hover {
        opacity: 0.9;
        .header-phone_icon {
            color: red;
        }
    }
    &_icon {
        margin-right: 10px;
    }
    @media (min-width: $md2 + px) {
        font-size: 20px;
    }
}

.burger {
    display: none;
    position: relative;
    z-index: 2;
    @include burger;
    &:not(:last-child) {
        margin-right: 20px;
    }
    @media (max-width:$md3 + px) {
        display: block;
    }
  }